@import "./colors.scss";
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;1,400&display=swap');

.contact {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: auto;
    width: 100%;
    max-width: 1444px;
    margin: 0 auto;
    font-family: 'Lato', sans-serif;
    color: #555;

    &_form {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        width: 50%;
        padding: 50px;
        border-right: 1px solid #e1e1e1;

        &_box {
            display: flex;
            flex-direction: column;
            align-self: stretch;

            &_input, &_textarea {
                margin: 10px 0;
                padding: 10px;
                border: 1px solid #e1e1e1;
                border-radius: 5px;
            }

            &_textarea {
                min-height: 100px;
                resize: vertical;
            }
        }

        h2 {
            margin-bottom: 40px;
            font-weight: normal;
        }
        p{
            line-height: 1.6;
            margin-bottom: 40px;
        }

        h2::after {
            content: '';
            display: block;
            height: 10px;
            width: 100px;
            border-bottom: 3px solid $ctaColor;
        }

        &_button {
            display: flex;
            align-self: stretch;
            justify-content: center;
            margin-top: 40px;
            padding: 15px;
            color: white;
            background-color: $ctaColor;
            border: none;
            border-radius: 5px;
            text-align: center;
            cursor: pointer;
        }

    }

    &_info {
        width: 50%;
        padding: 50px;
        border-top: none;

        &_social {
            margin-top: 40px;
            width: 100%;

            svg {
                width: 50px;
                margin-right: 30px;
            }
        }

        h2 {
            font-weight: normal;
            margin-bottom: 40px;
        }

        h2::after {
            content: '';
            display: block;
            height: 10px;
            width: 50px;
            border-bottom: 3px solid $ctaColor;
        }

        p {
            line-height: 30px;

            a {
                color: #555;
            }
        }
    }
}

#map {
    height: 400px;
    width: 100%;
  }
  

@media screen and (max-width: 900px) {
    .contact {
        flex-direction: column;

        &_form {
            width: 100%;
        }

        &_info {
            width: 100%;
            border-top: 1px solid #e1e1e1;;
        }
    }
}